/* LABEL */
.label {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  color: #2c3e50;
  font-weight: bold;
  font-size: 16px;
}

/* INPUTS */
.input {
  text-align: center;
  text-transform: uppercase;
  color: #2c3e50;
  font-size: 16px;
  font-weight: bold;
  background-color: #d5d8dc;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: none;
  box-shadow: transparent;
  min-height: 50px;
  min-width: 100px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  transition: 0.3s;
}

.input:hover {
  text-align: center;
  text-transform: uppercase;
  color: #2c3e50;
  font-weight: bold;
  background-color: #abb2b9;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

