/* ANIMAÇÕES */
.fade-in {
  animation-name: fadein;
  animation-duration: 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade-out {
  animation-name: fadeout;
  animation-duration: 0.5s;
  animation-delay: 2s;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.toast {
  animation: toast 3s ease forwards;
}

@keyframes toast {
  0%,
  100% {
    opacity: 0;
  }
  10%,
  90% {
    opacity: 1;
  }
}

