.rbt-menu.dropdown-menu.show {
  justify-content: center;
  background-color: #fadbd8;
  border-radius: 5px;
  border: none;
  border-color: transparent;
  max-height: 200px !important;
  outline: none;
  outline-color: transparent;
  padding-left: 0px;
  padding-right: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
  margin-left: 0px;
  margin-right: 0px;
  margin-top: 5px;
  margin-bottom: 0px;
  justify-content: center;
  text-align: center;
}

.dropdown-item:not(hover) {
  max-width: 100%;
  font-weight: bold;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  transition: 0.5s;
}

